.form {
    display: flex;
  }
  
  .form input {
    flex: 1; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
    font-size: 1rem;
    outline: none;
    border: none;
    border-bottom: 1px solid #d6555581;
  }
  
  .create-button {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: 1rem;
    background: #d6555581;
    border-radius: 3px;
    color: #ffffff;
    font-weight: 500;
    cursor: pointer;
  }
  
  .create-button:hover {
    background: #94141473;
  }