.todo-list-template {
    background: rgb(255, 255, 255);
    width: 512px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    margin: 0 auto; /* 페이지 중앙 정렬 */
    margin-top: 4rem;
}

.title {
    padding: 2rem;
    font-size: 2.5rem;
    text-align: center;
    font-weight: 100;
    background: #dbaaaa;;
    color: white;
}
  
.form-wrapper {
    padding: 1rem;
    border-bottom: 1px solid #cf2222; 
}
  
.todos-wrapper {
    min-height: 15rem;
}